import React from 'react';
import Navbar from '../../components/Navbar';
import Button from 'react-bootstrap/Button';
import $ from 'jquery';
import '../../css/App.css';
import { useEffect } from 'react';
import colorspectrum from '../../images/resized/Spectrum.png';
import exampleImage from '../../images/resized/Elephant2.jpg';
import { useNavigate } from 'react-router-dom';

const Home = () => {
	useEffect(() => {
		createPixelMask();
	}, [])

	let navigate = useNavigate();

	function createPixelMask(){
		var table = document.getElementById("pixel_Table");
		var row;
		var cell;
    	for(var i=0;i<7;i++){
			row = table.insertRow(i);
      		for(var j=0;j<7;j++){
			    cell = row.insertCell(j);
			    cell.addEventListener('click', IncreaseOpacity);
				cell.addEventListener('contextmenu', DecreaseOpacity)
        	}
    	}

    	document.getElementById("pixel_Table").style.backgroundImage = 'url(' + exampleImage + ')';
    }
		
	function DecreaseOpacity(event) {
		event.preventDefault()
		var opacity_level = parseFloat($(event.target).css("opacity"));
		if (opacity_level > "0.0") {
			opacity_level = opacity_level - 0.30;
			$(event.target).css("opacity", opacity_level);
		}
	}
		
	function IncreaseOpacity(event) {
		var opacity_level = parseFloat($(event.target).css("opacity"));
		if (opacity_level < "0.9") {
			opacity_level = opacity_level + 0.30;
			$(event.target).css("opacity", opacity_level);
		}	
	}

    return (
        <div className="home">		
            <Navbar/>
            <div style={{height: "40vw", position: "relative"}}>
                <div style={{position: "absolute", top: "50%", left: "50%", transform: "translate(-50%, -50%)", textAlign: "center"}}>
					<div className="row">
						<div style={{float: "left", width: "50%", textAlign: "left"}}>
							<p>Enkäten består utav två delar. I den första delen kommer du få se <b>5 bilder</b> på olika djur. Som respondent vill vi att du med hjälp utav ett rutnät markera vilka delar av en bild som du anser är viktig för att du ska kunna identifiera djuret. Den andra delen ber dig att <b>rangordna</b> olika förklaringar, samt svara på <b>X frågor</b>.</p>
							<br></br>
							<p>För den första delen av enkäten så vill vi förtydliga att det inte är ditt uppdrag att identifiera djuren, utan att indikera vilka delar av en bild du behöver, och till vilken grad, för att utföra identifikationen. Detta gör du genom att klicka på rutorna i bilden. Det finns <b>4 nivåer</b> av markering, där ingen markering är nivå 1</p>
							<br></br>
							<p>I exempelbilden till höger kan du klicka runt och testa hur den första delen kommer att fungera. Det finns inget minimalt eller maximalt antal av rutorna som måste markeras, men vi ser gärna att du markerar <b>mer än 1 ruta</b> och försöker titta på varje ruta individuellt för att avgöra hur viktig den är. För elefanten skulle man till exempel kunna markera rutorna som inkluderar snabeln och ge högre nivåer av markering där det är tydligare att det är en snabel man tittar på.</p>
							<br></br>
							<p>
								Har du frågor innan du påbörjar enkäten kan du kontakta oss på
								<br>
								</br>
								xaiundersokning@gmail.com
							</p>
							<br></br>	
						</div>
						<div style={{float: "left", width: "50%"}}>
							<h5>Exempel</h5>
							<table id="pixel_Table"></table>
							<p>
								<small>
									<i>
										Vänsterklick ökar nivån för en ruta med ett steg, medan 
										<br>
										</br>
										högerklick minskar ett steg. Högre nivå indikerar att
										<br>
										</br>
										rutan är viktigare för att kunna identifiera djuret.
									</i>
								</small>
							</p>
							<figure>
							<img src={colorspectrum} alt="Spektrum"/>
								<figcaption>
									<h5>
										Gradskala
									</h5>
								</figcaption>
							</figure>
							<p>
							Tryck på “Starta enkäten” när du är redo.
							</p>
							<Button variant="outline-secondary" onClick={() => {
								navigate('/survey');
							}}
							>Starta enkäten</Button> 	
						</div>
					</div>
					
                </div> 
            </div>          
        </div>
    )
}

export default Home;