import Button from 'react-bootstrap/Button';
import { useNavigate } from 'react-router-dom';
import { auth, provider} from '../../configs/firebaseConfig';
import { useEffect } from "react";
import '../../css/App.css';

const Login = () => {
  let navigate = useNavigate();

  useEffect(() => { 
    document.getElementById('Login-button').disabled = true;
  },[])

  async function signInWithGoogle() {
    auth
      .signInWithPopup(provider)
      .then((res) => {
      ;
          navigate("/home");
      })
      .catch((error) => {
        console.log(error);
      });

    }
  return (
    <div className="login">
      <div style={{height: "40vw", position: "relative"}}>
        <div style={{position: "absolute", top: "50%", left: "50%", transform: "translate(-50%, -50%)", transform: "translate(-50%, -50%)"}}>
          <div>
            <h1>Välkommen till denna enkät om Explainable AI</h1>
            <br></br>
            <p>Du behöver inte ha några förkunskaper inom AI för att utföra enkäten. Enkäten syftar till att utforska vilka likheter och skillnader som kan ses mellan mänsklig objektklassificering och Explainable AI-metoder.</p>
            <p> Explainable AI är ett område med fokus på att skapa mänsklig förståelse för hur en AI-modell fungerar. Målet är att ge insikt i vad som påverkade en AI:s beslut och hur det påverkades. Detta görs genom algoritmer som, efter att den AI modellen är färdigtränad, 
              retroaktivt försöker lista ut hur det fattar sina beslut. Många metoder för detta finns, men de flesta baseras på att man på olika sätt manipulerar delar av in-datan och observerar hur det förändrar beslutet.</p>
            <br></br>
            <p>Vårt mål är att se likheter och skillnader i vad dessa Explainable AI-algoritmer får fram som de viktigaste aspekterna i objekt-klassificering hos en eller flera AI-modeller och jämföra detta med vad människor prioriterar när de presenteras med samma klassificeringsproblem. 
              På detta sätt kan vi hitta eventuella mönster som kan antyda vilka Explainable AI-metoder som är mest lika mänskligt resonerande.</p>
            <p> Utifrån detta undersöker vi om just dessa metoder också är mer givande för att öka människors förståelse för en tränad AI-modell.</p>
            <br>
            </br>
            <h6>Den enda data som sparas är dina enkätsvar, vilka är anonyma.
              <br>
              </br>
              Enkäten tar ca <u>X</u> minuter att genomföra.
              <br>
              </br>
              <br>
              </br>
              Stort tack för din tid.
            </h6>
            <br>
            </br>
            <input type="checkbox" id="checkbox" onClick={ () => {
              if(document.getElementById('checkbox').checked) {
                document.getElementById('Login-button').disabled = false;
              }
              else {
                document.getElementById('Login-button').disabled = true;
              }
            }}
            >            
            </input>
            <label>
              <i>
              Jag har tagit del av informationen ovan och samtycker till att delta. Jag är	medveten om att mitt deltagande är helt frivilligt och att	jag kan avbryta mitt deltagande i studien utan att ange något skäl.
              </i>
              </label>
            <br>
            </br>
          </div>
          <Button id="Login-button" style={{ marginTop: "20px"}} variant="outline-secondary" onClick={ () => {
            signInWithGoogle();
          }}
          >
            Logga in med Google
          </Button>
        </div>
      </div>
    </div>
  )
}

export default Login;